<template>
  <WebMap
    :config="getOptions"
    :data="data"
    @mapCreated="mapCreated"
    @changeData="getData"
    @mapMounted="mapMounted"
    @click="getCurrentLocal"
  ></WebMap>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      currentLocal: { name: "", value: [] },
    };
  },
  async created() {
    this.getCurrentLocal();
    this.getData(1);
  },
  methods: {
    getCurrentLocal() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          function successCallback(position) {
            const { latitude, longitude } = position.coords; // 
            this.currentLocal.value = [longitude, latitude];
          },
          function errorCallback(error) {
            console.log("" + error.message);
          }
        );
      } else {
        console.log("");
      }
    },
    async getData(url) {
      let res = await this.$api.getMap(url);
      if (res.data) {
        this.data = res.data.map((item) => ({ ...item, value: item.descript }));
      }
    },
    mapCreated() {},
    mapMounted(Map) {
      Map.on("georoam", function (params) {
        var option = Map.getOption();
        if (params.zoom != null && params.zoom != undefined) {
          option.geo[0].zoom = option.series[0].zoom;
          option.geo[0].center = option.series[0].center;
        } else {
          option.series[0].center = option.geo[0].center;
        }
        Map.setOption(option);
      });
    },
    getOptions(data) {
      return {
        title: {
          text: "geo + map",
          subtext: "",
        },
        tooltip: {
          formatter: function (params) {
            return ['<div style="max-width:200px;">' + params + "</div>"];
          },
        },
        geo: [
          {
            map: "china",
            zoom: 1.14,
            top: "10%",
            right: "16%",
            left: "16%",
            bottom: "10%",
            roam: true,
            itemStyle: {
              areaColor: "#e5ecf9",
              shadowColor: "rgba(158,201,243,0.85)",
              borderColor: "rgba(183,219,255,1)",
              shadowOffsetX: 9,
              shadowOffsetY: 9,
              emphasis: {
                areaColor: "rgba(229,236,249,1)",
                borderWidth: 0,
                label: {
                  show: true,
                },
              },
            },
            regions: [
              {
                name: "",
                itemStyle: {
                  areaColor: "rgba(0, 10, 52, 1)",
                  borderColor: "rgba(0, 10, 52, 1)",
                  opacity: 0,
                  label: {
                    show: true,
                    color: "#009cc9",
                  },
                },
              },
            ],
          },
        ],
        series: [
          {
            map: "china", //
            type: "map",
            zoom: 1.14,
            top: "10%",
            right: "16%",
            left: "16%",
            bottom: "10%",
            roam: true,
            ...{
              // 
              label: {
                show: true,
                color: "rgba(130,143,200,1)",
              },
              itemStyle: {
                borderWidth: 1,
                borderColor: "rgba(196,207,254,1)",
                areaColor: "rgba(229,236,249,1)",
              },
            },
            emphasis: {
              // hover
              label: {
                show: true,
                color: "rgba(130,143,200,1)",
              },
              itemStyle: {
                borderColor: "rgba(196,207,254,1)",
                borderWidth: 1,
                areaColor: "rgba(229,236,249,1)",
              },
            },
            ...{
              // 
              selectedMode: "single",
              select: {
                label: {
                  color: "#333",
                },
                itemStyle: {
                  areaColor: {
                    type: "linear",
                    x: 0,
                    y: 0,
                    x2: 0,
                    y2: 1,
                    colorStops: [
                      { offset: 0, color: "rgba(66,99,232,1)" },
                      { offset: 1, color: "rgba(55,183,249,1)" },
                    ],
                    
                  },
                },
              },
            },
            data,
          },
          {
            type: "effectScatter",
            coordinateSystem: "geo",
            showEffectOn: "render",
            zlevel: 1,
            symbolSize: function (val) {
              return [6, 3];
            },
            showEffectOn: "render",
            zlevel: 2,
            rippleEffect: {
              period: 2.5, //
              brushType: "stroke", //stroke()fill()
              scale: 6, //
            },
            hoverAnimation: true,
            label: {
              formatter: function ({ data }) {
                if (data.time) {
                  return "{text|" + data.name + "|" + data.time + "}";
                } else {
                  return "{text|" + data.name + "}";
                }
              },
              distance: 18,
              position: "top",
              show: true,
              textStyle: {
                rich: {
                  text: {
                    color: "#fff",
                    backgroundColor: "#aad1ff",
                    borderWidth: 1,
                    borderType: "solid",
                    borderColor: "#4263e8",
                    boxShadow: "0px 1px 4px #7998d6",
                    fontSize: 14, //
                    padding: [4, 4, 4, 4],
                  },
                },
              },
            },
            itemStyle: {
              color: "#0579FA", //
            },
            data: data.map((item) => ({
              ...item,
              label: {
                formatter: "{b}",
                color: "red",
              },
              itemStyle: {
                color: "#96cc34",
              },
            })),
          },
          {
            type: "lines",
            zlevel: 2,
            effect: {
              show: true,
              period: 8, //
              trailLength: 0, //[0,1]
              symbol: "triangle", //
              symbolSize: [30, 30], //
            },
            lineStyle: {
              normal: {
                color: "#1DE9B6",
                width: 0, //
                opacity: 0, //
                curveness: -2, //
              },
            },
            // data,
          },
          {
            type: "lines",
            zlevel: 3,
            effect: {
              show: true,
              period: 8, //
              trailLength: 0.7, //[0,1]
              symbol: "arrow", //
              symbolOffset: ["34%", "-50%"],
              symbolSize: [10, 10], //
            },
            lineStyle: {
              color: {
                type: "linear",
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  { offset: 0, color: "rgba(255 255 0 ,1)" },
                  { offset: 0.5, color: "rgba(255 255 0 ,0.2)" },
                  { offset: 1, color: "rgba(255 255 0 ,0)" },
                ],
                
              },
              width: 0.3, //
              opacity: 0.3, //
              curveness: -2, //
            },
            data: data
          },
        ],
      };
    },
  },
};
</script>
