<template>
  <div ref="map" class="map"></div>
</template>

<script>
export default {
  mounted() {
    let {
      Map,
      View,
      layer: { Tile: TileLayer },
      source: { XYZ },
      proj: { fromLonLat },
    } = ol;
    const key = "get_your_own_D6rA4zTHduk6KOKTXzGB";
    const attributions = "";
    const map = new Map({
      layers: [
        new TileLayer({
          source: new XYZ({
            attributions: attributions,
            url: "http://t3.tianditu.com/DataServer?T=img_w&x={x}&y={y}&l={z}&tk=5a257cd2df1b3311723bd77b0de14baf",
            tileSize: 512,
          }),
        }),
      ],
      target: this.$refs.map,
      view: new View({
        center: fromLonLat([112.91, 28.21], "EPSG:4326", "EPSG:3857"),
        zoom: 5,
      }),
    });
    map.on("loadstart", function () {
      map.getTargetElement().classList.add("spinner");
    });
    map.on("loadend", function () {
      map.getTargetElement().classList.remove("spinner");
    });
    var source = new ol.source.Vector({
      wrapX: false,
    });
    map.addLayer(
      new ol.layer.Vector({
        source: source,
      })
    );

    function addRandomFeature() {
      var geom = new ol.geom.Point(ol.proj.fromLonLat([112.91, 28.21]));
      var feature = new ol.Feature(geom);
      source.addFeature(feature);
    }

    var duration = 1000;

    function flash(feature) {
      var start = new Date().getTime();
      //
      var listenerKey = map.on("postcompose", animate);

      function animate(event) {
        //
        var vectorContext = event.vectorContext;
        //
        var frameState = event.frameState;
        //OpenLayers.Geometry
        var flashGeom = feature.getGeometry().clone();
        //
        var elapsed = frameState.time - start;
        //
        var elapsedRatio = elapsed / duration;
        // radius530
        var radius = ol.easing.easeOut(elapsedRatio) * 25 + 5;
        //
        var opacity = ol.easing.easeOut(1 - elapsedRatio);
        //Circle
        var style = new ol.style.Style({
          image: new ol.style.Circle({
            radius: radius,
            stroke: new ol.style.Stroke({
              color: "rgba(255, 0, 0, " + opacity + ")",
              width: 1 + opacity,
            }),
          }),
        });
        //
        vectorContext.setStyle(style);
        //
        vectorContext.drawGeometry(flashGeom);
        if (elapsed > duration) {
          start = frameState.time;
        }
        //
        map.render();
      }
    }
    //
    source.on("addfeature", function (e) {
      flash(e.feature);
    });

    addRandomFeature();
  },
};
</script>