<template>
  <WebMapConfig :data="data" :params="params"></WebMapConfig>
</template>

<script>
export default {
  data() {
    return {
      data: [],
      params: {
        themes: 3,
        type: "china",
        options: {
          title: {
            text: "3D",
            subtext: "",
          },
        },
      },
    };
  },
  async created() {
    let res = await this.$api.getMap(1);
    if (res.data) {
      this.data = res.data.map((item) => ({
        ...item,
        filter: "3",
        value: item.descript,
      }));
    }
  },
  methods: {
    mapCreated(echarts) {
      echarts.registerMap("china", this.$china2);
    },
    mapMounted(e) {},
  },
};
</script>
