<template>
  <div class="content">
    <div ref="map" class="map"></div>
    <button ref="btn"></button>
  </div>
</template>
  
  <script>
export default {
  async mounted() {
    // 
    let res = await this.$axios({
      type: "get",
      url: this.$withBase("/data/1.json"),
    });
    let points = res.data.map((i) =>
      ol.proj.transform(i, "EPSG:4326", "EPSG:3857")
    );
    // 
    var route = new ol.geom.LineString(points);
    var routeCoords = route.getCoordinates();
    var routeLength = routeCoords.length;

    // 
    var routeFeature = new ol.Feature({
      type: "route",
      geometry: route,
    });

    // 
    var geoMarker = new ol.Feature({
      type: "geoMarker",
      geometry: new ol.geom.Point(routeCoords[0]),
    });

    // 
    var styles = {
      route: new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 5,
          color: "blue",
        }),
      }),
      geoMarker: new ol.style.Style({
        image: new ol.style.Circle({
          radius: 10,
          snapToPixel: false,
          fill: new ol.style.Fill({
            color: "red",
          }),
        }),
      }),
    };

    var center = points[0];
    var animating = false;
    var speed, now;

    // 
    var tileLayer = new ol.layer.Tile({
      source: new ol.source.OSM(),
    });

    // 
    var vectorLayer = new ol.layer.Vector({
      source: new ol.source.Vector({
        features: [routeFeature, geoMarker],
      }),
      style: function (feature) {
        if (animating && feature.get("type") === "geoMarker") {
          return null;
        }
        return styles[feature.get("type")];
      },
    });

    // 
    var map = new ol.Map({
      target: this.$refs.map,
      layers: [tileLayer, vectorLayer],
      view: new ol.View({
        center: center,
        zoom: 17,
      }),
    });

    // 
    var moveFeature = function (event) {
      var vectorContext = event.vectorContext;
      var frameState = event.frameState;
      if (animating) {
        var elapsedTime = frameState.time - now;
        var index = Math.round((speed * elapsedTime) / 1000);
        if (index >= routeLength) {
          stopAnimation(true);
          return;
        }
        var currentPoint = new ol.geom.Point(routeCoords[index]);
        var feature = new ol.Feature(currentPoint);
        vectorContext.drawFeature(feature, styles.geoMarker);
      }
      map.render();
    };

    // 
    function startAnimation() {
      if (animating) {
        stopAnimation(false);
      } else {
        animating = true;
        now = new Date().getTime();
        speed = 50;
        geoMarker.setStyle(null);
        map.getView().setCenter(center);
        map.on("postcompose", moveFeature);
        map.render();
      }
    }

    // 
    function stopAnimation(ended) {
      animating = false;
      var coord = ended ? routeCoords[routeLength - 1] : routeCoords[0];
      geoMarker.getGeometry().setCoordinates(coord);
      map.un("postcompose", moveFeature);
    }

    // 
    this.$refs.btn.addEventListener("click", function () {
      startAnimation();
    });
  },
};
</script>
<style>
.content {
  width: 100%;
  height: 100%;
}
.map {
  width: 100%;
  height: 400px;
}
</style>