<template>
  <div>
    <div ref="map" class="map"></div>
    <button ref="btn"></button>
  </div>
</template>
  
  <script>
export default {
  async mounted() {
    // 
    let res = await this.$axios({
      type: "get",
      url: this.$withBase("/data/1.json"),
    });
    let points = res.data;
    // 
    var route = new ol.geom.LineString(points);
    var routeCoords = route.getCoordinates();
    var routeLength = routeCoords.length;

    // 
    var routeFeature = new ol.Feature({
      type: "route",
      geometry: route,
    });

    // 
    var geoMarker = new ol.Feature({
      type: "geoMarker",
      geometry: new ol.geom.Point(routeCoords[0]),
    });

    // 
    var styles = {
      route: new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 5,
          color: "blue",
        }),
      }),
      geoMarker: new ol.style.Style({
        image: new ol.style.Circle({
          radius: 10,
          snapToPixel: false,
          fill: new ol.style.Fill({
            color: "red",
          }),
        }),
      }),
    };

    var center = [13451034.958002912, 3594986.8806004445];
    var animating = false;
    var speed, now;

    // 
    var tileLayer = new ol.layer.Tile({
      source: new ol.source.OSM(),
    });

    // 
    var markerLayer = new ol.layer.Vector({
      source: new ol.source.Vector(),
      style: new ol.style.Style({
        image: new ol.style.Circle({
          radius: 20,
          fill: new ol.style.Fill({
            color: "red",
          }),
        }),
      }),
    });

    // 
    var lineLayer = new ol.layer.Vector({
      source: new ol.source.Vector(),
      style: new ol.style.Style({
        stroke: new ol.style.Stroke({
          width: 5,
          color: "blue",
        }),
      }),
    });
    // 
    var map = new ol.Map({
      target: this.$refs.map,
      layers: [tileLayer, markerLayer, lineLayer],
      view: new ol.View({
        center: center,
        zoom: 17,
      }),
    });
    // 
    var interval;
    var index = 0;
    var markerFeature;
    // 
    this.$refs.btn.addEventListener("click", function () {
      // 
      if (interval != undefined) {
        clearInterval(interval);
      }

      // 
      markerLayer.getSource().clear();
      lineLayer.getSource().clear();
      index = 0;

      // 
      if (points.length > 1) {
        var lineFeature = new ol.Feature({
          geometry: new ol.geom.LineString(points),
        });
        lineLayer.getSource().addFeature(lineFeature);
      }

      // 
      interval = setInterval(function () {
        if (index < points.length) {
          // 
          markerLayer.getSource().clear();

          // 
          var currentPoint = points[index];
          markerFeature = new ol.Feature({
            geometry: new ol.geom.Point(currentPoint),
          });
          markerLayer.getSource().addFeature(markerFeature);

          // +1
          index++;
        } else {
          clearInterval(interval);
          index = 0;
        }
      }, 10);
    });
  },
};
</script>
<style>
.map {
  width: 100%;
  height: 400px;
}
</style>